import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { Badge, UncontrolledDropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import PropTypes from 'prop-types';

import { AppAsideToggler, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';



class DefaultHeader extends Component {

  constructor(props){
      super(props)

      this.logout = this.logout.bind(this)

      this.state = {
        user: props.user
      }
  }

  logout(){
    localStorage.setItem("access_token", "a");
    window.location.reload();
  }

  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppSidebarToggler className="d-md-down-none" display="lg" />
        <Nav navbar>
          <UncontrolledDropdown nav direction="down" className="mr-2">
            <DropdownToggle nav>
              <div>
                {this.state.user.first_name + " " + this.state.user.last_name + " " + this.state.user.class}<br />
                {this.state.user.school}<br />
              </div>
            </DropdownToggle>
          </UncontrolledDropdown>
          <NavItem className="d-md-down-none">
            <NavLink to="#" onClick={e => this.logout()} className="nav-link"><i className="cui-account-logout"></i></NavLink>
          </NavItem>
        </Nav>
      </React.Fragment>
    );
  }
}


export default DefaultHeader;
